import React from 'react';
import { Table, Empty } from 'antd';
import { CheckOutlined, CloseOutlined } from '@ant-design/icons';

class Grid extends React.Component {
    render = () => (
        <Table title={() => <div className="api-title">{this.props.header ? '' : ''}</div>} columns={[{
            title: this.props.header ? '' : '',
            dataIndex: 'name',
            key: 'name',
        }, {
            title: '',
            dataIndex: 'type',
            key: 'type',
        }, {
            title: '',
            dataIndex: 'require',
            key: 'require',
            render: require => require ? <CheckOutlined /> : <CloseOutlined />
        }, {
            title: '',
            dataIndex: 'description',
            key: 'description',
        }]} dataSource={this.props.data} rowKey="name" pagination={false}
            locale={{ emptyText: <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} description={this.props.header ? '' : ''} /> }} />
    );
}

export default Grid;