import React from 'react';
import { Space, Alert } from 'antd';
import Grid from './grid';

class Dict extends React.Component {
    render = () => (
        <Space direction="vertical" style={{ width: '100%' }}>
            <Alert type="info" message={'' + this.props.url + '/dict/list'} />
            <Grid header={true} data={[{
                name: 'photon-session-id',
                type: 'string',
                require: true,
                description: 'SESSION ID' + localStorage.getItem('photon-session-id') + ''
            }]} />
            <Grid header={false} data={[{ name: 'key', type: 'string', require: true, description: '' + this.props.meta.key + '' }]} />
            <div></div>
            <pre>{'[{\n    "value": "",\n    "name": ""\n}]'}</pre>
        </Space>
    );
}

export default Dict;