import React from 'react';
import { Space, Alert, Divider } from 'antd';
import Grid from './grid';

class Upload extends React.Component {
    responseFile = `{
    "path": "URI",
    "fileName": "",
    "fileSize": "",
    "success": "true-false-"
}`;

    responseBase64 = `{
    "code": 0,
    "data": {
        "path": "URI",
        "fileName": "",
        "fileSize": "",
        "success": "true-false-"
    }
}`;

    render = () => (
        <Space direction="vertical" style={{ width: '100%' }}>
            <Divider dashed={true}></Divider>
            <Alert type="info" message={'' + this.props.url + '/photon/ctrl-http/upload'} />
            <Grid header={true} data={this.props.meta.headers} />
            <Grid header={false} data={[{ name: this.props.meta.upload, type: 'file', require: true, description: '' }]} />
            <div></div>
            <pre>{this.responseFile}</pre>
            <Divider dashed={true}>Base64</Divider>
            <Alert type="info" message={'' + this.props.url + '/photon/ctrl/upload'} />
            <Grid header={true} data={this.props.meta.headers} />
            <Grid header={false} data={[{ name: 'name', type: 'string', require: true, description: '' + this.props.meta.upload + '' },
            { name: 'contentType', type: 'string', require: true, description: 'image/jpeg' },
            { name: 'fileName', type: 'string', require: true, description: '' },
            { name: 'base64', type: 'string', require: true, description: 'BASE64Content-Type' }]} />
            <div></div>
            <pre>{this.responseBase64}</pre>
            <Divider dashed={true}>cURL</Divider>
            <Alert type="info" message={'' + this.props.url + '/photon/ctrl-http/upload'} />
            <div></div>
            <pre>curl -H 'photon-session-id: {localStorage.getItem('photon-session-id')}' -F '{this.props.meta.upload}=@/path/to/file' {this.props.url}/photon/ctrl-http/upload</pre>
            <div></div>
            <pre>{this.responseFile}</pre>
        </Space>
    );
}

export default Upload;